
.vux-1px,.vux-1px-t,.vux-1px-b,.vux-1px-tb,.vux-1px-l,.vux-1px-r{position:relative
}
.vux-1px:before{content:" ";position:absolute;left:0;top:0;width:200%;height:1px;border:1px solid #C7C7C7;color:#C7C7C7;height:200%;transform-origin:left top;transform:scale(.5)
}
.vux-1px-t:before{content:" ";position:absolute;left:0;top:0;right:0;height:1px;border-top:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 0;transform:scaleY(.5)
}
.vux-1px-b:after{content:" ";position:absolute;left:0;bottom:0;right:0;height:1px;border-bottom:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 100%;transform:scaleY(.5)
}
.vux-1px-tb:before{content:" ";position:absolute;left:0;top:0;right:0;height:1px;border-top:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 0;transform:scaleY(.5)
}
.vux-1px-tb:after{content:" ";position:absolute;left:0;bottom:0;right:0;height:1px;border-bottom:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 100%;transform:scaleY(.5)
}
.vux-1px-l:before{content:" ";position:absolute;left:0;top:0;width:1px;bottom:0;border-left:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 0;transform:scaleX(.5)
}
.vux-1px-r:after{content:" ";position:absolute;right:0;top:0;width:1px;bottom:0;border-right:1px solid #C7C7C7;color:#C7C7C7;transform-origin:100% 0;transform:scaleX(.5)
}
body{background-color:#eee
}
#channel{height:100%;background-color:#fff
}
#channel .vux-header{background:rgba(255,255,255,0);color:#000;position:fixed;width:100%;z-index:99;overflow:hidden
}
#channel .vux-header .vux-header-title{color:#000
}
#channel .content{padding-bottom:80px
}
#channel .content .large-img img{width:100%
}
#channel .content .channels-info{padding-left:.3rem;padding-right:.3rem
}
#channel .content .channels-info li{padding-bottom:12px
}
#channel .content .channels-info li .title{font-size:16px;font-weight:bold;line-height:40px;color:#444
}
#channel .content .channels-info li .con{font-size:14px;line-height:23px;color:#9d9d9d
}
#channel .content .channels-info li .newupdate li{cursor:pointer;overflow:hidden;padding:16px 0;position:relative;height:80px
}
#channel .content .channels-info li .newupdate li img{float:left;width:120px;height:80px;border-radius:3px;display:block
}
#channel .content .channels-info li .newupdate li .title{line-height:22px;font-size:15px;color:#686868;font-weight:400;padding-left:11px;overflow:hidden
}
#channel .content .channels-info li .newupdate li .date{position:absolute;bottom:15px;width:100%;text-align:right;display:block;color:#a8a8a8
}
#channel footer{height:46px;width:100%;max-width:680px;background-color:#fff;box-shadow:rgba(0,0,0,0.2) 0 0 10px;position:fixed;bottom:0;padding:0;z-index:1000;display:-webkit-box;-webkit-box-align:center;font-size:20px
}
#channel footer .freeread{color:#fbfbfb;width:50%;text-align:center;background-color:#757475;height:100%;line-height:46px
}
#channel footer .subscribe{width:50%;color:#fbfbfb;line-height:46px;text-align:center;background-color:#ffbf00
}


